















/* 移动端1025px时切换 background-size*/
.appBackground[data-v-045bce6d] {
  z-index: -1;
  overflow: hidden;
  top: 0;
  height: 100%;
  position: absolute;
  width: 100%;
  background-size: 100% 100%;
  background-image: url(../../assets/img/background/stripes-bg.jpg);
}
.appBackground[data-v-045bce6d]:before {
  z-index: -1;
  position: absolute;
  font-size: 100%;
  z-index: 1;
  content: '';
  left: 0;
  right: 0;
  top: 0;
  background-size: 75.7% 50%;
  height: 100%;
  background-repeat: no-repeat;
  background-image: url(../../assets/img/background/folds-top.jpg);
}
.appBackground[data-v-045bce6d]:after {
  z-index: -1;
  position: absolute;
  content: '';
  left: 0;
  right: 0;
  bottom: 0;
  height: 20%;
  background-size: 100% 100%;
  background-image: url(../../assets/img/background/footer-background-main.jpg);
}



